.scard {
    position: relative;
    /* z-index: 1; */
    top: -100px;
    width: 300px;
    min-width: 300px;
    border-radius: 8px;
    padding: 32px;
    padding-top: 48px;
    padding-bottom: 48px;
    background: #001D37;
    box-shadow: 0px 9px 23px rgba(0, 0, 0, 0.25);
    text-align: left;
    margin-bottom: 2.5em;
    color: white;
    left: 50%;
    transform: translate(-50%);

}

@media (min-width: 500px) {

    .scard {
        width: 80%;
